
<template>
    <div id='beingHit'>
         <!-- 海报轮播 Swiper-->
          <div class="swiper-container poster">
              <div class="swiper-wrapper">
                  <div class="swiper-slide">
                    <img src="../../assets/img/三生三世.jpg">
                  </div>
                  <div class="swiper-slide">
                    <img src="../../assets/img/杀破狼海报.jpg">
                  </div>
                  <div class="swiper-slide">
                     <img src="../../assets/img/破局海报.jpg">
                  </div>
              </div>
              <!-- Add Pagination -->
              <div class="swiper-pagination"></div>
          </div>



          <!-- 正在热映电影列表 -->
          <div class="movie-list">
            <div class="movie-item" v-for='(Item,index) in jsondata.subjects'>
                <router-link :to="{path:'/detail/'+Item.id}">

                    <!-- 电影子块海报 -->
                    <div class="item-poster">
                      <img v-bind:src="Item.images.large">
                    </div>

                    <!-- 电影子块文字内容 -->
                    <div class="item-content">
                      <p>{{Item.title}}</p>
                      <div class="score-box">
                        <Star class='star-box' :rating="Item.rating.average" v-if="Item.rating.average"></Star>
                        <span v-if="Item.rating.average">{{Item.rating.average}}</span>
                      </div>
                      <p>导演：&nbsp{{Item.directors[0].name}}</p>
                      <p>主演：&nbsp<span v-for='casts in Item.casts'>{{casts.name}}/</span></p>
                      <p>{{Item.collect_count}}人看过</p>
                    </div>

                    <!-- 电影子块购买按钮 -->
                    <div class="item-btn">
                      <mt-button plain size="small" v-if='Item.rating.average'>购买</mt-button>
                      <mt-button plain class='btn-yellow' size="small" v-else>预售</mt-button>
                    </div>
                </router-link>
            </div>
        
          
          </div>
        <!--   <button class="button">点击获取数据</button> 
          <div class="user"></div> -->

    </div>
</template>
<script>
    import Vue from 'vue'
    import jsonp from '@/Js/json.js'

    import { Swipe, SwipeItem } from 'mint-ui';
    import { Button } from 'mint-ui';

    // 引用JQuery
    import $ from 'jquery'

    Vue.component(Button.name, Button);
    Vue.component(Swipe.name, Swipe);
    Vue.component(SwipeItem.name, SwipeItem);

    import { api } from '@/global/api.js'  //导入静态资源'
    import Star from '@/components/star' //导入星星打分组件
    import Swiper from '../../../static/Js/swiper-3.4.2.min.js'//导入Swiper插件
    // $(function(){
    //       $(".button").on("click",function(){
    //         $.ajax({
    //           type:"get",
    //           url:"https://way.jd.com/jisuapi/get?channel=科技&num=20&start=0&appkey=eabc39d0aabddd8378606f2d01f03a4b",
    //           dataType:"jsonp",
    //           jsonp:"callback",
    //           jsonpCallback:"success_jsonpCallback",
    //           success:function(json){
    //             console.log(444);
    //           },
    //           error:function(){
    //             alert("请求出错！");
    //           }
    //         });
    //       });
    //     });

    export default
        {
            created() {
                this.request();
                // this.getNews();
            },
            mounted(){
                // this.getData();
                var swiper = new Swiper('.swiper-container', {
                    pagination: '.swiper-pagination',
                    effect: 'flip',
                    grabCursor: true,
                    loop:true, 
                });
            },
            data() {
                return {               
                    jsondata: {},
                    jsonNews: {}
                }
            },
            components:{
                Star:Star
            },
            methods: {
                // 请求豆瓣正在热映接口数据
                request() {
                    let loading = Vue.prototype.$loading({text:"玩命加载中..."});
                    jsonp('https://api.douban.com/v2/movie/in_theaters', {}, function (data) {
                      loading.close();//结束loading效果
                        this.jsondata = data;
                        console.log(this.jsondata);
                    }.bind(this));
                   
                }
                // getNews() {
                //     jsonp('https://way.jd.com/jisuapi/get?channel=科技&num=20&start=0&appkey=eabc39d0aabddd8378606f2d01f03a4b', {}, function (data) {
                //         this.jsonNews = data;
                //         console.log('新闻信息',this.jsonNews);
                //     }.bind(this));
                   
                // }
            },
            watch: {
                //监测$route对象，如果发生改变，就触发request方法
                "$route":'request'
            }
        }
</script>
<style>
#beingHit .poster{
  width: 100%;
  height: 3.5rem;
  /*border: 1px solid #000;*/
  box-sizing: border-box;
  margin-top: 5px;
  margin-bottom: 20px;
}
#beingHit .poster img{
  width: 100%;
  height: 3.5rem;
}
#beingHit .movie-list{
  width: 100%;
  height: auto;
  /*border: 1px solid #000;*/
  box-sizing:border-box;
}
#beingHit .movie-item{
  width: 100%;
  height: 3.55rem;
  border-bottom: 1px solid #ccc;
  padding: 15px 0.1rem 0.6rem 0.2rem;
  box-sizing:border-box;
}
/*电影列表海报小图*/
#beingHit .item-poster{
  width: 28%;
  height: 3rem;
  /*border: 1px solid #000;*/
  float: left;
}
#beingHit .item-poster img{
  width: 100%;
  height: 3rem; 
}
/*电影列表文字内容*/
#beingHit .item-content{
  width: 53%;
  height: 2.3rem;
  /*border: 1px solid #000;*/
  float: left;
}
#beingHit .item-content p:nth-child(1){
    margin-left: 0.3rem;
    margin-bottom: 5px;
    font-weight: bolder;
    font-size: 0.35rem;
}
#beingHit .star-box{
  margin-left: 0.3rem;
  margin-bottom: 6px;
  width: 1.5rem;
  float: left;
}
#beingHit .score-box{
  width: 100%;
  height: 0.38rem;
  font-size: 0.26rem;
  color:#777676
}
#beingHit .item-content p:nth-child(3),#beingHit .item-content p:nth-child(4){
    margin-left: 0.3rem;
    margin-bottom: 0.06rem;
    font-size: 0.26rem;
    color:#777676;
}
#beingHit .item-content p:nth-child(5){
    margin-left: 0.3rem;
    margin-bottom: 0.06rem;
    font-size: 0.26rem;
}


/*电影列表购买按钮*/
#beingHit .item-btn{
  width: 16%;
  height: 2.3rem;
  /* border: 1px solid pink; */
  float: left;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding-top: 0rem;
  padding-left: 0rem;
  margin-left: 0.2rem;
}
#beingHit .mint-button--default.is-plain {
    border: 1px solid #d02f5e;
    color: #d02f5e;
}
#beingHit .btn-yellow{
    border: 0.02rem solid orange !important;
    color: orange !important;
    border-radius: 0.08rem;
}
#beingHit .mint-button--small {
    font-size: 0.24rem;
    padding: 0 0.2rem;
    height: 0.6rem;
    font-weight: bold;
}
/*路由链接*/
#beingHit a:-webkit-any-link {
    color: inherit;
    cursor: auto;
    text-decoration: underline;
}


</style>
   



